/* Fonts */
@font-face {
  font-family: 'Fira Code';
  src: url('./assets/fonts/FiraCode-Medium.eot');
  src: url('./assets/fonts/FiraCode-Medium.eot?#iefix') format('embedded-opentype'),
      url('./assets/fonts/FiraCode-Medium.woff2') format('woff2'),
      url('./assets/fonts/FiraCode-Medium.woff') format('woff'),
      url('./assets/fonts/FiraCode-Medium.ttf') format('truetype'),
      url('./assets/fonts/FiraCode-Medium.svg#FiraCode-Medium') format('svg');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Fira Code';
  src: url('./assets/fonts/FiraCode-Regular.eot');
  src: url('./assets/fonts/FiraCode-Regular.eot?#iefix') format('embedded-opentype'),
      url('./assets/fonts/FiraCode-Regular.woff2') format('woff2'),
      url('./assets/fonts/FiraCode-Regular.woff') format('woff'),
      url('./assets/fonts/FiraCode-Regular.ttf') format('truetype'),
      url('./assets/fonts/FiraCode-Regular.svg#FiraCode-Regular') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Fira Code';
  src: url('./assets/fonts/FiraCode-Bold.eot');
  src: url('./assets/fonts/FiraCode-Bold.eot?#iefix') format('embedded-opentype'),
      url('./assets/fonts/FiraCode-Bold.woff2') format('woff2'),
      url('./assets/fonts/FiraCode-Bold.woff') format('woff'),
      url('./assets/fonts/FiraCode-Bold.ttf') format('truetype'),
      url('./assets/fonts/FiraCode-Bold.svg#FiraCode-Bold') format('svg');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}


body, * {
  font-family: 'Fira Code', sans-serif !important;
}
/* Reset and base styles  */
* {
	padding: 0px;
	margin: 0px;
	border: none;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

/* Links */

a, a:link, a:visited  {
    text-decoration: none;
}

a:hover  {
    text-decoration: none;
}

/* Common */

aside, nav, footer, header, section, main {
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
    font-size: inherit;
	font-weight: inherit;
}

ul, ul li {
	list-style: none;
}

img {
	vertical-align: top;
}

img, svg {
	max-width: 100%;
	height: auto;
}

address {
  font-style: normal;
}

/* Form */

input, textarea, button, select {
	font-family: inherit;
    font-size: inherit;
    color: inherit;
    background-color: transparent;
}

input::-ms-clear {
	display: none;
}

button, input[type="submit"] {
    display: inline-block;
    box-shadow: none;
    background-color: transparent;
    background: none;
    cursor: pointer;
}

input:focus, input:active,
button:focus, button:active {
    outline: none;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

label {
	cursor: pointer;
}

legend {
	display: block;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
